.tattoo-post {

    position: fixed;
    top:  0 ;
    bottom: 0;
    left:0;
    right: 0;
    z-index: 999;
    display: flex;
    justify-content: center;
    align-items: center;


}



.tatoo-post-bg {
    background-color: rgb(0, 0, 0);
    position: absolute;
    top: 0;
    bottom: 0;
    left:0;
    right: 0 ;
   
    


}

.tatoo-post-content {
    position: relative;
    max-width:  1200px;
    background-color: rgb(52, 56, 52);



}

.tatoo-post-content img {
    display: block;
    max-width: 100%;
    max-height: calc(100vh-120px);
    margin-left: auto;
    margin-right: auto;
}

.tatoo-post-content h4{
    color: white;
    text-align: center;

}

.tattoo-post p {
    position: absolute;
    top : 0;
    right: 0%;
    color: rgb(203, 207, 210);
    margin-top: 0;
    border: 1px solid rgb(95, 98, 84);
    background-color: rgb(100, 96, 90);
}